import React, { Component } from 'react'
import { Col, Row } from 'shards-react';
import { connect } from 'react-redux';
import { actionSetParentScada } from '../../../redux/actions/elementAction'
import { actionGetPermission } from '../../../redux/actions/authAction'
import ImageFormAdd from '../../layout/image/ImageFormAdd';
import ImageFormEdit from '../../layout/image/ImageFormEdit';
import LabelFormAdd from '../../layout/label/LabelFormAdd';
import LabelFormEdit from '../../layout/label/LabelFormEdit';
import img from '../../../images/empty.png'
import Image from '../../layout/image/Image';
import ImageAdd from '../../layout/image/ImageAdd';
import Label from '../../layout/label/Label';
import LabelAdd from '../../layout/label/LabelAdd';
import Line from '../../layout/line/Line';
import ForceElement from '../../layout/ForceElement';
import InputWordAdjustClick from '../../ScadaComponent/InputWordAdjustClick';
import InputDWordAdjustClick from '../../ScadaComponent/InputDWordAdjustClick';
import { backgroundClick } from '../../../redux/actions/backgroundAction'
import LineAdd from '../../layout/line/LineAdd';
import LineFormAdd from '../../layout/line/LineFormAdd'
import * as GetData from '../../layout/GetData/FirebaseGetData';
