import WordDisplayFormAdd from '../../ScadaComponent/WordDisplay/WordDisplayFormAdd'
import DWordDisplayFormAdd from '../../ScadaComponent/DWordDisplay/DWordDisplayFormAdd'
import WordAdjustFormAdd from '../../ScadaComponent/WordAdjust/WordAdjustFormAdd'
import BitAdjustFormAdd from '../../ScadaComponent/BitAdjust/BitAdjustFormAdd'
import DWordAdjustFormAdd from '../../ScadaComponent/DWordAdjust/DWordAdjustFormAdd'
import WordDisplayAdd from '../../ScadaComponent/WordDisplay/WordDisplayAdd'
import DWordDisplayAdd from '../../ScadaComponent/DWordDisplay/DWordDisplayAdd'
import WordAdjustAdd from '../../ScadaComponent/WordAdjust/WordAdjustAdd'
import DWordAdjustAdd from '../../ScadaComponent/DWordAdjust/DWordAdjustAdd'
import BitDisplayFormEdit from '../../ScadaComponent/BitDisplay/BitDisplayFormEdit'
import WordDisplayFormEdit from '../../ScadaComponent/WordDisplay/WordDisplayFormEdit'
import DWordDisplayFormEdit from '../../ScadaComponent/DWordDisplay/DWordDisplayFormEdit'
import WordAdjustFormEdit from '../../ScadaComponent/WordAdjust/WordAdjustFormEdit'
import BitAdjustFormEdit from '../../ScadaComponent/BitAdjust/BitAdjustFormEdit'
